import React, {Component} from "react"
import "./InicialNav.css"
import logo from './vuhu_logo.jpg';
import { Switch, Route, Link, Redirect, withRouter } from 'react-router-dom';
import AuthService  from '../../utils/AuthService';

const AuthButton = withRouter(({ history }) => (
  localStorage.getItem('jwt') !== null
    ? <Link to="/" onClick={() => {
	      AuthService.signout(() => {history.push('/')
			      });
			    }}>
			<div className= "header__blocks"> 
				<p className="text">Log Out</p>
			</div>
		</Link>
    : 
    <Link to="/login"><div className= "header__blocks"> <p className="text">Log in</p></div></Link>
));

class InicialNav extends Component{
	render(){
		return(
			<React.Fragment>
					<div className= "header__blocks"> 
							<Link to="/"> 
							<img id="logo__white" src={logo}  alt="logo white"/>
							</Link>
						</div>

				<header className="header__buttons">
						
					
					<Link to="/"> 
						<div className= "header__blocks"> 
							<p className="text__red" >VUHU</p>
						</div>
					</Link>


					<Link to="/terms">
						<div className= "header__blocks"> 
							<p className="text">Terminos</p>
						</div>
					</Link>

					 <a href='http://vuhu-vuhu.blogspot.com/' target="blank" >
						<div className= "header__blocks"> 
							<p className="text">Blog</p>
						</div>
					</a>
					
					<Link to='/jewelry/joyeria'>
					<div className= "header__blocks">
						<p className="text">Joyeria</p>
					</div>
					 </Link>

				
					<Link to="/jewelry/dijes">
					<div className= "header__blocks"> 
						<p className="text">Dijes</p>
					</div>
					</Link>

			
					<Link to="/jewelry/collares">
					<div className= "header__blocks"> 
						<p className="text">Collares</p>
					</div>
					</Link>

					
					<AuthButton />
					
					<div className= "header__blocks__blue" 
					onClick={()=>{this.props.changeState()}}
					> 
						<p className="text">Cart</p>
						<i className="fa fa-shopping-cart" aria-hidden="true"></i>
					</div>

					</header>
			</React.Fragment>
		);
	}
}


export default InicialNav


