import React, {Component} from "react"
import "./Body.css"
import request from 'superagent';
import { Link } from 'react-router-dom';

class Body extends Component{
	constructor(props){
		super(props);
		this.state = {
			products:[]
		}
	}

	componentDidMount() {
	this.fetchApi();
  	}

	fetchApi = () =>{
	 const ENDPOINT = `http://localhost:3000/api/v1/jewelry`
		request
			.get(ENDPOINT)
			.then(response=> {
				this.setState({
					products:response.body.jewelry,
				})	
	
			});
	}
	render(){
		
		
		return(
			<div className="body__landing"> 
					<div className="body__featured">
					<h3 className="featured">Nuestras obras de arte </h3>
					<p className="featured__uno"></p>
				</div>

				<div className="packer">
				{ this.state.products.map((product,index) =>{
					
					return(

							<Link to={`/Item/${product._id}`} key={product._id}>
								<div className="block" key={product._id}>
									<div className="block__image">
										<img className="url__api" src={product.imageLink}  alt=""/>	
									</div>
									
									<div>
										
									</div>	
									<div className='block__under'>
										
										<p className="text__under">{product.item}</p>
										<p className="text__under__price"> MXN {product.price}</p>
										
									</div>
								</div>
							</Link>
					)})}				
				

				</div>

			</div>
		);
	}
}

export default Body;


