import Terms from       "./components/Terms/Terms";
import Jewelry from     "./components/Jewelry/Jewelry";
import Item from        "./components/Item/Item";
import ShoppingCart from "./components/ShoppingCart/ShoppingCart"
import React, { Component } from 'react';
import                  './App.css';
import InicialNav from  "./components/InicialNav/InicialNav";
import Body from        "./components/Body/Body";
import { Switch, Route, Link, Redirect, withRouter } from 'react-router-dom';
import FooterEnd from   "./components/FooterEnd/FooterEnd"
import Button     from  '@material-ui/core/Button';
import AuthService  from './utils/AuthService';
import Login        from './components/Login';


const PrivateRoute = ({ component: Component, ...rest }) => (
  <Route {...rest} render={(props) => (
    // AuthService.isAuthenticated === true
    localStorage.getItem('jwt') !== null
      ? <Component {...props} />
      : <Redirect to={{
        pathname: '/login',
        state: {
          from: props.location
        }
      }} />
  )} />
);
const AuthButton = withRouter(({ history }) => (
  // AuthService.isAuthenticated === true
  localStorage.getItem('jwt') !== null
    ? <Button color='inherit' onClick={() => {
      AuthService.signout(() => {
        history.push('/')
      });
    }}>Logout</Button>
    : <Button component={ Link } to='/login' color='inherit'>Login</Button>
));

class App extends Component {

  constructor(props){
    super();
    this.state = {
      cartList:[],
      push:false,
      open: false,
      cartCount:{}
    }

    this.saveToCart=this.saveToCart.bind(this)
    this.changeState=this.changeState.bind(this)
    this.actualizarCount = this.actualizarCount.bind(this)
  }

  saveToCart(id){
    this.setState(prevState => ({ 
      cartList: [...prevState.cartList, id] 
    }));
    setTimeout(this.actualizarCount, 1);   
  }

  actualizarCount(){
    var counts = {};
    this.state.cartList.forEach(function(x) { counts[x] = (counts[x] || 0)+1; });
    this.setState(prevState=> ({cartCount:counts}) ) 
  }

  changeState(){
    this.setState(prevState =>({
      push:!this.state.push
    }));
    this.actualizarCount();
  }

  render() {
    return (
      <div className="app__container">
        <InicialNav changeState={this.changeState}/>
        <ShoppingCart 
          cartList={this.state.cartList}  
          cartCount={this.state.cartCount}
          actualizarCuenta={this.actualizarCount}
          check={this.state.push} 
          changePush={this.changeState}
          saveFromCart={this.saveToCart}
          />
        <Switch>
        	<PrivateRoute exact path='/' component={Body} /> 
          <Route path='/login' exact component={ Login } />
        	<Route exact path='/terms' component={Terms} /> 
        	<Route path='/jewelry/:category' component={Jewelry} /> 
          <Route  path="/Item/:item" 
                  render={(props) =>  
                    <Item {...props} 
                      changeStateFromItem={this.changeState}
                      saveFunction={this.saveToCart}
                      countAgain={this.actualizarCount}
                    />}
                  />
        </Switch>
        <FooterEnd />
        <Route path='/privacy-policy' component={() => window.location = 'https://example.zendesk.com/hc/en-us/articles/123456789-Privacy-Policies'}/>
     

      </div>
    );
  }
}

export default App;



