import React, { Component } from 'react';
import request from 'superagent';
import './CardItem.css'

class CardItem extends Component{
	constructor(props){
		super()
		this.state={
			image:"",
			name:"",
			price:"",
			ultimate:[]
		}

	}

	componentDidMount() {
	this.fetchApi();
 	}


	fetchApi = () =>{

	 const ENDPOINT = `http://localhost:3000/api/v1/jewelry/${this.props.id}`
		request
			.get(ENDPOINT)
			.then(response=> {
				this.setState({
					image:response.body.jewelry[0].imageLink,
					name:response.body.jewelry[0].item,
					price:response.body.jewelry[0].price,
					ultimate:response.body.jewelry[0],    
					
				})		
				
			});
	
	}


	render(){
	
		return(
	
			<div className="item__cart" >
					
					<div className="div__image">
						<img src={this.state.image} alt="x" className="img__cart" />
					</div>
					<div className="in__cart">
						<div>

							<p className="text__cart__color">
								<i className="fa fa-plus" 
								aria-hidden="true"
								onClick={ () => {console.log('plus onClick'); this.props.saveFromCart(this.props.id) }} 
								></i>
								{this.props.number}pz 
								
								<i className="fa fa-minus" 
								aria-hidden="true"
								onClick={ () => {console.log('minus onClick')}}
								></i>

							</p> 

							<p className="text__cart">   {this.state.name}
							</p>
						</div>
						<div>
							<p className="text__cart__color__bis"> Subtotal {this.state.price} mxn x {this.props.number}pz =  $  {this.state.price * this.props.number}</p>
						</div>
					</div>

				</div>
	

		);
	}	
}

export default CardItem;


