import React,{Component} from "react";
import { Link } from 'react-router-dom';
import "./FooterEnd.css";
import logo from './vuhu_logo.jpg';

class FooterEnd extends Component{
	render(){
		return(
			<React.Fragment>
				
							<img id="logo__white__footer" src={logo}  alt="logo white"/>
					
					<div className='footer-horizontal'>
			          
			          <div className="foot__left">
			          <p className='footer-section-title'>Company</p>
			          <a href='http://vuhu-vuhu.blogspot.com/' target="blank" > <p>Blog</p></a>
			          <Link className='footer-link-decoration' to='/terms'><p>Terms + Conditions</p></Link>
			          </div>
			    
			          <div className="foot__right">
			            <p className='footer-section-title'>Social</p>
			            <div>
			              <ul className='grid'>
			                <li>
			                	<a href="https://www.instagram.com/vuhu_vuhu/" target="blank" >		                
			                	<i className="fa fa-instagram"></i>
			                	</a>
			                </li>
			                <li>
			                	<a href="https://twitter.com/VuhuJoyeriaeco" target="blank" >		                
			                	<i className="fa fa-twitter"></i>
			                	</a>
			                </li>
			                <li>
			                	<a href="https://www.tumblr.com/login?redirect_to=%2Fblog%2Fvuhujoyeriaecologicablr" target="blank" >		                
			                	<i className="fa fa-tumblr"></i>
			                	</a>
			                </li>
			              </ul>
			            </div>
					  </div>					  
					</div>
			</React.Fragment>
		);
	}
}

export default FooterEnd;


