import React, { Component } from 'react';
import "./ShoppingCart.css";
import request from 'superagent';
import CardItem from "./CardItem"

class ShoppingCart extends Component{
	constructor(props){
		super(props)
		this.state = {
			push:false,
			cart:{},
		}
	}

	changeState(){
		this.setState(prevState =>({
			push:!this.state.push
		}))
	}

	componentDidUpdate(prevProps) {

	  if (this.props.cartCount !== prevProps.cartCount) {
	    
	    this.setState(prevState =>({
					cart:this.props.cartCount
				}))
	    this.renderCardItems();
	  }
	}

	renderCardItems(){
			var p = this.props.cartCount;
			const saveFromCart = this.props.saveFromCart

 			return Object.keys(p).map(function (key,index) {
  					 return  <CardItem id={key} number={p[key]}key={key} saveFromCart={saveFromCart} /> })
	}

	render(){
		return(
			<div className= {this.props.check ?  "shopping__div" : "shopping__div__close"} >
				<div className="close__cart">
						<button 
						className="x__cart"
						onClick={ this.props.changePush}
						> &times;
						</button>
						<div className="head__cart">
							<h3 className="head__cart__text">Tu Carrito</h3>
							<h3 className="head__cart__text__btn"> <button>Paga aquí </button></h3>
						</div>
				</div>

				<div>
					{this.renderCardItems()}
				</div>

			</div>
		);
	}
}

export default ShoppingCart;


