import React,{Component} from "react"
import "./Item.css"
import request from 'superagent';
//EN ESTE ITEM DEBE DE IR EL MODELO SOLO
import {
  TextField,
  Button,
  Snackbar
} from '@material-ui/core';

class Item extends Component{  //item = shoppinglist josh

	constructor(props){

		
		super(props);
		this.state = {
			item:{},
			onLoginError: false
		}
	}

	componentDidMount() {
	this.fetchApi();
	
  	}

	fetchApi = () =>{
	 const ENDPOINT = `http://localhost:3000/api/v1/jewelry/${this.props.match.params.item}`
		request
			.get(ENDPOINT)
			.then(response=> {
				
				this.setState({
					item:response.body.jewelry[0]  //
						//
				})		
				
			});
	
	}
	handleClose = (event, reason) => {
    this.setState({ onLoginError: false });
  };


  onLoginTrue= ()=>{
  	this.setState({ onLoginError:true})
  }
	
	render(){
		
		const features = this.state.item.features
		
		
		return(
			<div className="body__item">
				<div className="item__uno">
					<img id="img__uno" src={this.state.item.imageLink}  alt="uno"/>
				</div>
				
				<div className="item__dos">
					<div className="item__dos__title">
						<h4 className="it1">{this.state.item.item}</h4>
						<h4 className="it1__dos">{this.state.item.availability ? "Disponible inmediatamente" : "No disponible actualmente"}</h4>
						<h4 className="it1__dos"> Incluye: {features}</h4>
					</div>
					<div className="item__dos__subtitle">
						<p className="it2">MXN {this.state.item.price}</p>
					</div>
					
						<div className="item__dos__trio">
							<div className="triple"> 
								<p className="under">Categoria</p>
								<p className="condition"> {this.state.item.category}</p>
							</div>
							<div className="triple"> 
								<p className="under">Medidas</p>
								<p className="condition">
									 <span>Alt:{this.state.item.width}	 </span> 
									<span>Lar:{this.state.item.lenght} </span> 
									<span>Peso:{this.state.item.weight}g</span> 
								</p> 
							</div>
							<div className="triple__add"> 
								<button 								
								onClick={() => {   this.props.saveFunction(this.state.item._id); this.onLoginTrue(); this.props.countAgain();}}
								 className="addTo"> Add to Cart
								</button>	
							</div> 
							<Snackbar
					            anchorOrigin={{
					              vertical: 'bottom',
					              horizontal: 'center',
					            }}
					            open={ this.state.onLoginError }
					            autoHideDuration={ 2000 }
					            onClose={ this.handleClose }
					            ContentProps={{
					              'aria-describedby': 'message-id',
					            }}
					            message={<span id="message-id">Agregado al carrito </span>}
					          />
						</div>
					
				</div>
			</div>
		);
	}
}

export default Item;


